body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-style ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-justify-content: center;
          justify-content: center;
}

.nav-style li {
  margin-right: 20px;
  color: white;
  text-decoration: none;
  box-sizing: border-box;
  font-size: 20px;
  outline: 0;
  padding: 10px;
  height: 50px;
  width: 150px;
  border-radius: 10px;
  background-color: #075157
  
}

.nav-style a {
  color: white;
  text-decoration: none !important;
}

header {
  display: -webkit-flex;
  display: flex
}

.logo {
  width: 60px;
  height: 60px;
}


body {
  background-color: #8a9ab9;

}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: top;
          justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

a {
  color: white;
  cursor: pointer;
}

fieldset {
  border: 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#form {
  margin: 30px auto;
  width: 380px; }

#form label {
  color: #093d41;
  display: inline-block;
  margin-bottom: 10px;
  text-align: left;
  width: 100%; }

#form input,
#form button {
  box-sizing: border-box;
  font-size: 20px;
  outline: 0;
  padding: 10px;
  height: 50px;
  width: 100%;
  border-radius: 10px;
}

#form button {
  background: #093d41;
  border: 1px solid #093d41;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  padding: 8px 16px;
  text-decoration: none;
  
  -webkit-transition: .3s ease background;
  
  transition: .3s ease background;
  width: 70%; }

#form button:hover {
  background: #04ddec;
  -webkit-transition: .3s ease background;
  transition: .3s ease background; }

button {
  box-sizing: border-box;
  font-size: 14px;
  outline: 0;
  padding: 4px;
  width: 70px; }
